<template>
  <div class="row">
    <div class="card col-md me-3" ref="bindemail">
      <div class="card-body">
        <div class="card-title">
          <h3>绑定邮箱</h3>
          <hr />
        </div>

        <div v-if="!bind">
          <div class="d-flex align-items-start mb-3">
            <vs-input primary placeholder="请输入您的邮箱" v-model="form.email">
              <template #icon>
                <i class="bx bx-envelope"></i>
              </template>
              <template v-if="!testEmail(form.email) || !form.email" #message-danger> 格式不正确 </template>
            </vs-input>
            <button class="ms-2 btn btn-primary text-nowrap p-2 fw-bold" :disabled="!form.verify.token || !testEmail(form.email) || !form.email || form.send" @click="sendCode">发送验证码{{ form.wait === 0 ? "" : ` (${form.wait})` }}</button>
          </div>

          <Vaptcha :passCallback="vpass" ref="vaptchaVc" style="line-height: normal" class="mt-3" />

          <div class="vs-input__message vs-input__message--danger" v-if="!form.verify.token">请先完成人机验证</div>
          <div class="mt-3">
            <vs-input danger placeholder="请输入验证码" v-model="form.code" :disabled="!form.verify.token || !form.send">
              <template #icon>
                <i class="bx bx-check-shield"></i>
              </template>
              <template v-if="!form.code" #message-danger> 必填项 </template>
            </vs-input>
            <button class="mt-2 btn btn-success w-100" :disabled="!testEmail(form.email) || !form.email || !form.verify.token || !form.send || !form.code" @click="bindEmail">绑定邮箱</button>
          </div>
        </div>
        <div v-if="bind">
          <!-- 已绑定 -->
          <div class="d-flex flex-column">
            <span class="text-success fs-5 fw-bold pb-2"><StatusIcon status="success"></StatusIcon> 您的邮箱已完成绑定</span>
            <span class="text-secondary fw-bold py-2">
              <i class="bx bxs-envelope"></i>
              {{ $store.state.userData.user.info.email }}
            </span>
            <button v-wave class="mt-3 btn btn-danger" @click="unbindEmail">解除绑定</button>
          </div>
        </div>
      </div>
    </div>
    <div class="card col-md mt-md-0 mt-3">
      <div class="card-body">
        <div class="card-title">
          <h3>绑定手机</h3>
          <hr />
        </div>
        <Card color="danger" class="w-100">
          <template #icon>
            <font-awesome-icon :icon="['fas', 'circle-exclamation']" />
          </template>
          <template #title>
            <h4 class="font-weight-bolder m-0 text-nowrap">暂停服务</h4>
          </template>
        </Card>
      </div>
    </div>
  </div>
</template>

<script>
import Vaptcha from "@/components/Vaptcha.vue";
import Card from "@/components/Card.vue";
import StatusIcon from "@/components/StatusIcon.vue";
import { bind_email, unbind_email } from "@/scripts/auth";
import pullData from "@/scripts/common/PullData";

export default {
  name: "EmailPhone",
  data() {
    return {
      form: {
        wait: 0,
        uname: "",
        email: "",
        code: "",
        send: false, // 是否已发送验证码
        verify: {
          server: "",
          token: "",
        },
      },
      timer: null,
    };
  },
  components: {
    Card,
    Vaptcha,
    StatusIcon,
  },
  computed: {
    bind() {
      return !!this.$store.state.userData.user.info.email;
    },
  },
  methods: {
    vpass(data) {
      this.form.verify.server = data.server;
      this.form.verify.token = data.token;
    },
    testEmail(email) {
      let re = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (re.test(email)) {
        return true;
      } else {
        return false;
      }
    },
    async sendCode() {
      if (!this.testEmail(this.form.email) || !this.form.email || !this.form.verify.token || this.form.send) return;

      const loading = this.$vs.loading({
        target: this.$refs.bindemail,
        text: "发送中...",
      });

      const resp = await bind_email({
        email: this.form.email,
        verify: {
          sr: this.form.verify.server,
          tk: this.form.verify.token,
        },
      });

      if (resp.status === 1) {
        this.$swal({
          icon: "success",
          title: "发送成功",
          text: "验证码已发送至您的邮箱，请重新完成人机验证",
        });
        this.resetVerify();
        this.form.send = true;
        this.openTimer();
      } else {
        this.$swal({
          icon: "error",
          title: "发送失败",
          text: resp.msg,
        });
        this.resetVerify();
      }

      loading.close();
    },
    async bindEmail() {
      if (!this.testEmail(this.form.email) || !this.form.email || !this.form.verify.token || !this.form.send || !this.form.code) return;

      const loading = this.$vs.loading({
        target: this.$refs.bindemail,
        text: "绑定中...",
      });

      const resp = await bind_email({
        email: this.form.email,
        verify: {
          sr: this.form.verify.server,
          tk: this.form.verify.token,
        },
        code: this.form.code,
      });
      if (resp.status === 1) {
        this.$swal({
          icon: "success",
          title: "绑定成功",
          text: "您的邮箱已绑定",
        });
        await pullData(true);
      } else {
        this.$swal({
          icon: "error",
          title: "绑定失败",
          text: resp.msg,
        });
        this.resetVerify();
      }

      loading.close();
    },
    async unbindEmail() {
      await this.$swal({
        icon: "warning",
        title: "解除绑定",
        text: "您确定要解除绑定吗？",
        showCancelButton: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      }).then(async (resp) => {
        if (resp.isConfirmed) {
          const loading = this.$vs.loading({
            target: this.$refs.bindemail,
            text: "解绑中...",
          });

          const resp = await unbind_email();
          if (resp.status === 1) {
            await pullData(true);
            this.$swal({
              icon: "success",
              title: "解除成功",
              text: "您的邮箱已解除绑定",
            });
          } else {
            this.$swal({
              icon: "error",
              title: "解除失败",
              text: resp.msg,
            });
            this.resetVerify();
          }

          loading.close();
        }
      });
    },
    openTimer() {
      this.form.wait = 60;

      this.timer = setInterval(() => {
        this.form.wait--;
        if (this.form.wait <= 0) {
          this.form.wait = 0;
          this.form.send = false;
          clearInterval(this.timer);
        }
      }, 1000);
    },
    resetVerify() {
      this.$refs.vaptchaVc.reset();
      this.form.verify.server = "";
      this.form.verify.token = "";
    },
  },
};
</script>

